import React from "react"
//import * as scss from "./ButtonLink.module.scss"

const ButtonLink = (props) => {
  let size = ""
  if (props.size === "small") {
    size = "bx--btn--sm"
  }

  return (
    <a href={props.href} target="_blank" rel="noreferrer" title={props.title} className={`bx--btn bx--btn--primary ${size}`} style={props.style}>{props.children}</a>
  )
}

export default ButtonLink



